<template>
	<div id="app" @touchstart='touchStartY=$event.touches[0].pageY' @touchend="touchEnd">
		<img class="cover" src="./assets/home/cover.gif">
		<transition :name="transitionName" mode="out-in">
		    <router-view class="router-view" />
		</transition>
        <img :class="['up-arrow','absolute',{'hidden-b2-4':!isLoaded},{'loaded':isLoaded}]" src="@/assets/home/up-arrow.png">
	</div>
</template>
<script>
    export default {
        name: 'App',
        components: {},
        data() {
            return {
                pathList: ['/', '/welcome', '/anigod','/mandy'],
                transitionName: '',
                nowPathIndex: 0,
                isLoaded: false,
                touchStartY: 0
            }
        },
        created() {},
        mounted() {
            this.nowPathIndex = 0
            this.$router.push(this.pathList[this.nowPathIndex])
            setTimeout(() => {
                this.isLoaded = true
            }, 3000)
        },
        destroyed() {},
        computed: {},
        watch: {},
        methods: {
            touchEnd(e) {
                if (!this.isLoaded) {
                    return
                }
                //最小有效滑动距离（Y轴像素差）
                var minDistance = 50
                var moveDistance = e.changedTouches[0].pageY - this.touchStartY
                if (Math.abs(moveDistance) < minDistance) {
                    console.log("滑动距离不够，不做处理")
                    return
                }
                if (moveDistance < 0) {
                    console.log("上滑")
                    this.transitionName = 'rotateX-next'
                    if (this.nowPathIndex < this.pathList.length - 1) {
                        this.nowPathIndex += 1
                    } else {
                        console.log("已经到最后一页了")
                        return
                    }
                } else {
                    console.log("下拉")
                    this.transitionName = 'rotateX-last'
                    if (this.nowPathIndex > 0) {
                        this.nowPathIndex -= 1
                    } else {
                        console.log("已经到第一页了")
                        return
                    }
                }
                this.isLoaded = false
                setTimeout(() => {
                    this.isLoaded = true
                }, 3000)
                this.$router.push(this.pathList[this.nowPathIndex])
            }
        }
    }
</script>
<style lang="scss">
	* {
		margin: 0;
		padding: 0;
	}

	#app {
		font-family: Avenir, Helvetica, Arial, sans-serif;
		-webkit-font-smoothing: antialiased;
		-moz-osx-font-smoothing: grayscale;
		text-align: center;
		width: 100%;
		height: 100%;
		position: fixed;
		top: 0;
		left: 0;
		overflow: hidden;

	}

	.router-view {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        flex-flow: column;
	    width: 800px;
	    max-width: 100%;
	    min-height: 100%;
	    height: 100%;
	    overflow: hidden;
        position: relative;
	}

	img.cover {
		width: 100%;
		height: 100%;
		position: fixed;
		left: 0;
		top: 0;
	}
    .absolute {
    	position: absolute;
    	margin: auto;
    	z-index: 2;
    }
    
    .hidden-b2-4 {
    	animation: hidden-b2-4 8s;
    	-webkit-animation: hidden-b2-4 8s;
    }
    
    .hidden-b4-6 {
    	animation: hidden-b4-6 8s;
    	-webkit-animation: hidden-b4-6 8s;
    }
    img.up-arrow {
    	height: 60px;
    	opacity: 1;
    
    	&.loaded {
    		animation: up-arrow 3s ease-out infinite;
    		-webkit-animation: up-arrow 3s ease-out infinite;
    	}
    }
</style>
